import { Component, OnInit, Input } from '@angular/core';

export enum ActionKind {
    NO_ACTION = 0,
    URL_LINK = 1,
    ROUTER_LINK = 2,
    CUSTOM_ACTION = 3
}

export interface IBreadcrumbItem {
    title: string;
    routerLink?: string;
    url?: string;
    action?: () => void;
    kind?: ActionKind;
}

/*
 Presumptions: 
 1. items are fixed at the moment of page init
*/

@Component({
    selector: 'polp-bs-breadcrumb',
    templateUrl: './breadcrumb.component.html',
    styleUrls: ['./breadcrumb.component.scss']
})
export class BreadcrumbComponent implements OnInit {

    @Input('items') items: IBreadcrumbItem[];

    interItems: IBreadcrumbItem[];
    activeItem: IBreadcrumbItem;

    constructor() {
        this.interItems = [];
        this.activeItem = null;
    }

    ngOnInit() {

        // Pre-process data
        this.items.forEach(e => {
            if (e.routerLink) {
                e.kind = ActionKind.ROUTER_LINK;
            } else if (e.url) {
                e.kind = ActionKind.URL_LINK;
            } else if (e.action) {
                e.kind = ActionKind.CUSTOM_ACTION;
            } else {
                e.kind = ActionKind.NO_ACTION;
            }
        });

        // Set up interItems and activeItem, and items are
        // fixed this moment and thus interItems and activeItem
        if (this.items.length > 0) {
            // Split the array
            for (let i = 0; i < this.items.length - 1; i++) {
                this.interItems.push(this.items[i]);
            }

            this.activeItem = this.items[this.items.length - 1];
        }
    }

}
